.contacts {
  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    width: 1150px;
    margin: 0 auto;
    max-height: 650px;
  }
}

.contacts__title-wrapper {
  margin-left: 30px;
  margin-right: 30px;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-left: 50px;
    margin-right: 50px;
    padding-top: 32px;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row-reverse;
    justify-content: flex-end;
    padding-top: 42px;
  }
}

.contacts__title {
  font-size: 18px;
  line-height: 30px;
  font-weight: $fw-bold;
  color: $black;
  margin-top: 38px;
  margin-bottom: 28px;

  @media (min-width: $tablet-width) {
    font-size: 43px;
    line-height: 50px;
  }

  @media (min-width: $desktop-width) {
    font-size: 53px;
    line-height: 57px;
  }
}

.contacts__title-svg {
  @media (min-width: $tablet-width) {
    background-image: url("../img/flag.svg");
    background-repeat: no-repeat;
    background-position: right center;
    background-size: auto;
    width: 50px;
    height: 81px;
    margin-right: 3px;
    margin-bottom: 3px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 60px;
    margin-right: 64px;
  }
}

.contacts__button {
  display: block;
  font-size: 18px;
  line-height: 20px;
  font-weight: $fw-bold;
  padding: 14px 10px;
  margin: 0 30px;

  @media (min-width: $tablet-width) {
    width: 230px;
    margin: 0 auto;
    font-size: 17px;
    box-sizing: border-box;
    padding-left: 8px;
    padding-top: 15px;
    padding-bottom: 13px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 225px;
  }
}

.contacts__button-wrapper {
  margin-top: 42px;
  margin-bottom: 39px;

  @media (min-width: $tablet-width) {
    margin-bottom: 57px;
  }

  @media (min-width: $desktop-width) {
    width: 50%;
    margin: 0;
  }
}
